import * as React from "react";
import * as convert from "xml-js";

import {
  DefaultButton,
  PrimaryButton
} from "office-ui-fabric-react/lib/Button";
import { CommandBarButton } from "office-ui-fabric-react/lib/Button";
import { CommandBar } from "office-ui-fabric-react/lib/CommandBar";
import {
  Dialog,
  DialogFooter,
  DialogType
} from "office-ui-fabric-react/lib/Dialog";
import { Icon } from "office-ui-fabric-react/lib/Icon";
import { TextField } from "office-ui-fabric-react/lib/TextField";

import {
  copyToClipboard,
  downloadTextFile,
  processFileAsync
} from "../utils/UtilsGeneral";
import { AuditCheck } from "./AuditCheck";

import previewXml from "./../images/preview-xml.png";

/*
interface IProps {}
 */

interface IState {
  currentFileData?: object;
  currentXml?: string;
  currentJson?: string;
  downloadReady?: boolean;
  fileName?: string;
  hideDialogClear?: boolean;
}

export default class MainBrowse extends React.Component<IState> {
  public state: IState;
  // public fileUploadRef: React.RefObject<HTMLInputElement> See issue #1
  public fileUploadRef: any;

  constructor(props: any) {
    super(props);
    this.state = {
      currentFileData: {},
      currentJson: "",
      currentXml: "",
      downloadReady: false,
      fileName: "",
      hideDialogClear: true
    };
    this.convertXml = this.convertXml.bind(this);
    this.uploadFile = this.uploadFile.bind(this);
    this.clickUpload = this.clickUpload.bind(this);
    this.clickDownload = this.clickDownload.bind(this);
    this.clearXml = this.clearXml.bind(this);
    this.openDialogClear = this.openDialogClear.bind(this);
    this.closeDialogAndClear = this.closeDialogAndClear.bind(this);
    this.closeDialogClear = this.closeDialogClear.bind(this);
    this.copyJsonToClipboard = this.copyJsonToClipboard.bind(this);
    this.titleChange = this.titleChange.bind(this);
    this.fileUploadRef = React.createRef();
  }

  public render() {
    // console.log(this.state, this.fileUploadRef);
    return (
      <div className="MainBrowse">
        <div className="ms-Grid" dir="ltr">
          <div className="ms-Grid-row">
            <CommandBar
              items={this.getItems()}
              farItems={this.getFarItems()}
              ariaLabel={
                "Use left and right arrow keys to navigate between commands"
              }
            />
            <p />
            <Dialog
              hidden={this.state.hideDialogClear}
              onDismiss={this.closeDialogClear}
              dialogContentProps={{
                subText:
                  "Are you sure you want to delete the XML input and JSON output?",
                title: "Clear Input",
                type: DialogType.normal
              }}
              modalProps={{
                isBlocking: false,
                styles: { main: { maxWidth: 450 } }
              }}
            >
              <DialogFooter>
                <PrimaryButton
                  onClick={this.closeDialogAndClear}
                  text="Clear"
                />
                <DefaultButton onClick={this.closeDialogClear} text="Cancel" />
              </DialogFooter>
            </Dialog>
          </div>
          <div className="ms-Grid-row">
            <div className="ms-Grid-col ms-sm12 ms-md12 ms-lg4">
              <div style={{ padding: "10px 0", textAlign: "left" }}>
                <div
                  style={{
                    cursor: "default",
                    display: "flex",
                    height: "20px",
                    paddingTop: "12px"
                  }}
                >
                  <Icon
                    iconName="ArrowDownRight8"
                    style={{
                      color: "#00656e",
                      fontSize: "16px",
                      padding: "0 5px"
                    }}
                  />
                  <div className="ms-font-m" style={{ fontFamily: "Segoe UI" }}>
                    Upload or Paste XML below
                  </div>
                </div>
              </div>
              <TextField
                style={{
                  backgroundImage: this.state.currentXml
                    ? ""
                    : `url('${previewXml}')`
                }}
                value={this.state.currentXml}
                onChange={this.onChange}
                inputClassName="textbox__input"
                multiline={true}
                autoAdjustHeight={true}
              />
            </div>
            <div className="ms-Grid-col ms-sm12 ms-md12 ms-lg4">
              <div style={{ padding: "10px 0", textAlign: "right" }}>
                <CommandBarButton
                  style={{ height: "32px" }}
                  disabled={!this.state.downloadReady}
                  iconProps={{ iconName: "ArrowUpRight8" }}
                  text="Copy JSON to Clipboard"
                  onClick={this.copyJsonToClipboard}
                />
              </div>
              <TextField
                value={this.state.currentJson}
                readOnly={true}
                inputClassName="textbox__output"
                placeholder="... {}"
                multiline={true}
                autoAdjustHeight={true}
              />
            </div>
            <div className="ms-Grid-col ms-sm12 ms-md12 ms-lg4">
              <div className="audit-container">
                <AuditCheck
                  name="XML Parsing"
                  passed={this.state.downloadReady}
                />
                <div className="vert-divider__8" />
                <AuditCheck name="Clearbox form V1 validation" passed={false} />
                <div className="vert-divider__8" />
                <AuditCheck name="Form fields integrity check" passed={false} />
                <div className="vert-divider__8" />
                <AuditCheck
                  name="Clearbox form V2 validation"
                  passed={this.state.downloadReady && false}
                />
              </div>
            </div>
          </div>
        </div>
        <h2 className="App-subtitle">Prettify JSON:</h2>
        <pre className="prettyprint lang-json linenums">
          {this.state.currentJson}
        </pre>
      </div>
    );
  }

  private onChange = (ev: any, newText: string): void => {
    this.setState({ currentXml: newText });
    this.convertXml(newText);
  };

  private getItems = () => {
    return [
      {
        iconProps: {
          iconName: "Upload"
        },
        key: "upload",
        name: "Upload XML (Onsite v1)",
        onClick: this.clickUpload
      },
      {
        key: "upload-proxy-item",
        name: "Upload XML (Onsite v1)",
        // See issue #1: Setting this.fileUploadRef.current implicitly is an anti-pattern
        onRender: () => (
          <input
            ref={input => (this.fileUploadRef.current = input)}
            style={{ display: "none" }}
            type="file"
            accept="application/xml"
            onChange={this.uploadFile}
          />
        )
      },
      {
        disabled: !this.state.downloadReady,
        iconProps: {
          iconName: "Download"
        },
        key: "download",
        name: "Download JSON (Onsite v2)",
        onClick: this.clickDownload
      },
      {
        disabled: !this.state.currentXml,
        iconProps: {
          iconName: "Delete"
        },
        key: "clear",
        name: "Clear",
        onClick: this.openDialogClear
      }
    ];
  };

  private getFarItems = () => {
    return [
      {
        key: "title",
        name: "Form Title",
        onRender: () => (
          <TextField
            value={this.state.fileName}
            onChange={this.titleChange}
            autoComplete="off"
            placeholder="Form Title"
            styles={{ root: { width: "500px", padding: "4px 100px 0 0" } }}
          />
        )
      },
      {
        ariaLabel: "Help",
        iconOnly: true,
        iconProps: {
          iconName: "Help"
        },
        key: "help",
        name: "Help",
        onClick: () => console.log("Help")
      }
    ];
  };

  private openDialogClear() {
    if (this.state.currentXml && this.state.currentXml.length > 50) {
      this.setState({ hideDialogClear: false });
    } else {
      // Just delete - assumes spaff data
      this.clearXml();
    }
  }
  private closeDialogAndClear() {
    this.setState({ hideDialogClear: true });
    this.clearXml();
  }
  private closeDialogClear() {
    this.setState({ hideDialogClear: true });
  }

  private copyJsonToClipboard() {
    if (this.state.currentJson && this.state.downloadReady) {
      copyToClipboard(this.state.currentJson);
    }
  }

  private clearXml() {
    this.setState({
      currentJson: "",
      currentXml: "",
      downloadReady: false,
      fileName: ""
    });
  }

  private uploadFile(clickEvent: EventInit) {
    processFileAsync(clickEvent).then(file => {
      if (file) {
        const newTitle = file.fileProps.name
          .split(".")
          .slice(0, -1)
          .join();

        this.setState({
          currentFileData: file.fileProps,
          currentXml: file.fileData,
          fileName: newTitle
        });
        this.convertXml(file.fileData);
      }
    });
  }

  private clickUpload() {
    if (this.fileUploadRef.current) {
      this.fileUploadRef.current.click();
    }
  }

  private clickDownload() {
    if (this.state.currentJson) {
      downloadTextFile(
        this.state.fileName + ".json",
        this.state.currentJson,
        "data:text/json;charset=utf-8,"
      );
    }
  }

  private convertXml(newText: string) {
    if (newText) {
      try {
        const json = convert.xml2json(newText, {
          compact: true,
          spaces: 4
        });
        // Purge empty object case
        if (json !== "{}") {
          this.setState({
            currentJson: json,
            // currentJsonObj: JSON.parse(json),
            downloadReady: true
          });
        }
      } catch (error) {
        // console.error(error);
        this.setState({
          currentJson: "Error parsing XML!\n" + error.message,
          downloadReady: false
        });
      }
    } else {
      this.setState({ currentJson: null, downloadReady: false });
    }
  }

  private titleChange({}, newValue: string) {
    this.setState({ fileName: newValue });
  }
}
