import * as React from "react";
import "./App.css";
import MainBrowse from "./interface/MainBrowse";

import onsiteLogo from "./images/converter-icon.png";
import reactLogo from "./images/logo.svg";

import { initializeIcons } from "@uifabric/icons";
initializeIcons();

import { loadTheme } from "office-ui-fabric-react/lib/Styling";

loadTheme({
  isInverted: true,
  palette: {
    black: "#1d1d1d",
    neutralDark: "#272727",
    neutralLight: "#eaeaea",
    neutralLighter: "#f4f4f4",
    neutralLighterAlt: "#f8f8f8",
    neutralPrimary: "#333333",
    neutralPrimaryAlt: "#4b4b4b",
    neutralQuaternary: "#d0d0d0",
    neutralQuaternaryAlt: "#dadada",
    neutralSecondary: "#858585",
    neutralTertiary: "#c2c2c2",
    neutralTertiaryAlt: "#c8c8c8",
    themeDark: "#004f56",
    themeDarkAlt: "#00656e",
    themeDarker: "#004f56",
    themeLight: "#c7e0f4",
    themeLighter: "#deecf9",
    themeLighterAlt: "#eff6fc",
    themePrimary: "#007b86",
    themeSecondary: "#459fa7",
    themeTertiary: "#73b7bd",
    white: "#ffffff"
  }
});

class App extends React.Component {
  public render() {
    return (
      <div className="App">
        <header className="App-header">
          <div className="ms-Grid" dir="ltr">
            <div className="ms-Grid-row">
              <div className="ms-Grid-col ms-sm12 ms-md12 ms-lg1">
                <div />
              </div>
              <div className="ms-Grid-col ms-sm12 ms-md12 ms-lg5">
                <img src={reactLogo} className="App-logo" alt="logo" />
                <p>Powered by React</p>
                <p>Created by Kier Major Projects Infrastructure</p>
              </div>
              <div className="ms-Grid-col ms-sm12 ms-md12 ms-lg5">
                <img
                  src={onsiteLogo}
                  className="Onsite-logo"
                  alt="onsite-logo"
                />
              </div>
              <div className="ms-Grid-col ms-sm12 ms-md12 ms-lg1">
                <div />
              </div>
            </div>
          </div>

          <h1 className="App-title">Clearbox Onsite form converter</h1>
        </header>
        <MainBrowse />
      </div>
    );
  }
}

export default App;
