import { Label } from "office-ui-fabric-react/lib/Label";
import * as React from "react";

interface IProps {
  name: string;
  passed?: boolean;
}

export const AuditCheck: React.FC<IProps> = props => {
  return (
    <div className={"audit-check " + (props.passed ? "passed" : "failed")}>
      <div className="audit-check__text">
        <Label>
          {props.name}
          <div className="ms-font-s ms-fontWeight-bold">
            {props.passed ? "passed" : "failed"}
          </div>
        </Label>
      </div>
    </div>
  );
};
